@keyframes spin {
    0% {
        transform: rotate( 0deg );
    }

    100% {
        transform: rotate( 360deg );
    }
}

.spinner {
    border: 4px solid rgba( 0, 0, 0, .1 );
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border-left-color: white;

    animation: spin .5s linear infinite;
}
